import React, { Component } from "react";

class About extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>About</h2>

          <h3>How long have you been working at Dotloop?</h3>
          <p>Three years! (And counting)</p>

          <h3>What is one of the best parts about working at Dotloop?</h3>
          <p>The standup desks. Catered lunches. The snacks...</p>

          <ul>
            <li>Health insurance</li>
            <li>Gym reimbursement</li>
            <li>Snacks</li>
            <li>ZG Week</li>
          </ul>

          <p>Oh! The people are nice too.</p>

          <h3>What has been the toughest part of learning to code?</h3>
          <p>Let me sleep on this question. I'll come back to it. Promise.</p>

          <h3>What part of coding gets you most excited?</h3>
          <p>
            I love working within a team, so thinking about possibly having an
            opportunity to build things with others is really exciting to me.
          </p>

          <h3>If you could go anywhere in the world, where would you go?</h3>
          <p>
            I can't wait to take a trip back to Santa Cruz in Cali. I had the
            opportunity to go there for soccer and I would like to go back.
          </p>
        </div>
      </div>
    );
  }
}

export default About;



