import React, { Component } from "react";
import "./App.css";
import Header from "./components/Header";
import Navigation from "./components/Navigation";
import Footer from "./components/Footer";
import { Route, Redirect, Switch } from "react-router-dom";
import Welcome from "./pages/Welcome";
import About from "./pages/About";
import May from "./pages/may";
import June from "./pages/june";
import July from "./pages/july";
// import August from "./pages/august";
import September from "./pages/september";
import October from "./pages/october";
import November from "./pages/november";
import December from "./pages/december";
import Post from "./components/Post";

class App extends Component {
  render() {
    return (
      <div className="App" id="backToTop">
        <Header />
        <Navigation />
        <Route exact path="/" render={() => <Redirect to="/welcome" />} />
        <Switch>
          <Route exact path="/welcome" component={Welcome} />
          <Route exact path="/about" component={About} />
          <Route exact path="/may" component={May} />
          <Route exact path="/june" component={June} />
          <Route exact path="/july" component={July} />
          {/* <Route exact path="/august" component={August} /> */}
          <Route exact path="/september" component={September} />
          <Route exact path="/october" component={October} />
          <Route exact path="/november" component={November} />
          <Route exact path="/december" component={December} />
          <Route
            exact
            path="/:month"
            render={() => "Nothing to see here. Shoo. Shoo!"}
          />
        </Switch>
        <Route path="/:month/:post" component={Post} />
        <Footer />
      </div>
    );
  }
}

export default App;



